<script setup lang="ts">
import { useDebounce } from "@vueuse/core";
import { useRouteQuery } from "@vueuse/router";
import useList from "~/composables/use-list";
import useUserStore from "~/stores/user";

const { t } = useI18n();
const userStore = useUserStore();
const search = useRouteQuery<string>("q", "");
const loading = ref(false);
const debouncedSearch = useDebounce(search, 300);

const items = computed(() => userStore.followedItems);
const filteredItems = useList(items, { searchProperty: "title", searchValue: search });
const onSearchClear = () => (search.value = "");
const { y: windowY } = useWindowScroll();
const isHeaderSearchShown = computed(() => windowY.value > 160);

watch(search, () => {
  loading.value = true;
});

watch(debouncedSearch, () => {
  loading.value = false;
});

definePageMeta({ middleware: "auth" });
</script>

<template>
  <AuthHeader
    :class="{ '!my-0': isHeaderSearchShown }"
    :transparent="!isHeaderSearchShown"
    fixed
    with-locale
  >
    <template #left>
      <div class="items-center hidden gap-2 px-6 lg:flex text-secondary">
        <IconChevronLeft :height="12" :width="12" />
        <BaseButton
          tag="nuxt-link"
          type="secondary"
          class="truncate"
          link
          :to="{ path: '/auth/sign-up/following/category/' }"
          name="back to all categories"
        >
          {{ t("pages.auth_sign_up.back_to_all_categories") }}
        </BaseButton>
      </div>
    </template>

    <template v-if="isHeaderSearchShown" #append-center>
      <div class="min-w-[5rem] sm:w-[25rem] md:w-[30rem] lg:w-[40rem] lg:-ml-[13rem] rtl:lg:mr-[6rem]">
        <BaseInput
          v-model="search"
          class="truncate"
          name="search"
          :placeholder="t('labels.search')"
          search
          @clear="onSearchClear()"
        />
      </div>
    </template>
  </AuthHeader>

  <BaseMain class="min-h-screen">
    <div class="pt-32 pb-12 form-column">
      <div class="w-[20rem] sm:w-[30rem] md:w-[35rem] lg:w-[40rem]">
        <h1 class="mb-10 text-3xl text-center font-bold text-main">
          {{ t("labels.following") }}
        </h1>

        <div class="px-2 mb-10 sm:px-0">
          <BaseInput
            v-model="search"
            class="truncate"
            name="search"
            :placeholder="t('labels.search')"
            search
            @clear="onSearchClear()"
          />
        </div>

        <ListIdentityItems
          :items="filteredItems"
          grid-class="clubs-grid"
        />
      </div>
    </div>
  </BaseMain>
</template>
